// @flow
export const moveBlue = (blue: number): Action => ({type: 'MOVE_BLUE', blue}) ;
export const moveRed = (red: number): Action => ({type: 'MOVE_RED', red}) ;
export const moveYellow = (yellow: number): Action => ({type: 'MOVE_YELLOW', yellow}) ;



